<template>
  <div id="gift">
    <common-header :title="'礼物包'" link="礼物记录" @linkClick="linkTo('GiftData')"></common-header>
    <div class="gift__tab">
      <span data-id='1' class="tab" :class="{'active': active == 1}" @click="changeType(1)">实物礼物</span>
      <span data-id='2' class="tab" :class="{'active': active == 2}" @click="changeType(2)">定制礼物</span>
      <span data-id='3' class="tab" :class="{'active': active == 3}" @click="changeType(3)">虚拟礼物</span>
    </div>
		
		<div class="list-wrap"
			v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="15"
      infinite-scroll-immediate-check="false">
	    <Gift v-if="active == 1" :index='index' :giftList = 'goodses'/>
	    <Gift type='DIY' v-else-if="active == 2" :index='index' :giftList = 'goodses'/>
	    <Gift @checked="getCheckedIndex" type='virtual' v-else-if="active == 3" :index='index' :giftList = 'goodses'/>
		</div>
		
		<div v-if="goodses.length <= 0">暂无数据</div>
		
    <div class="gift__bottom" v-show="active == 3">
      <div class="count">
        <span class="count__text">数量</span>
        <Counter :num="value" :max="5" @change="valueChange"/>
      </div>
      <div class="submit">兑换零钱</div>
    </div>
  </div>  
</template>

<script>
  import Gift from '@/components/Gift/Gift';
  import Counter from '@/components/Counter/Counter';

  export default {
    data() {
      return {
        active: '1',
        value: 1,
        index:1,
        goodses:[],
        PARAMS:{
        	pa:1,
        	li:9999,
        	ob:1,
        }
      }
    },
		created(){
      this.getGiftGoodsList(1)
		},
    methods: {
      changeType(type) {
        this.active = type;
        this.index = type;
        this.goodses = []
        document.documentElement.scrollTop = 0
				this.PARAMS = Object.assign({},this.PARAMS,{
					pa:1,
				})
      	switch(type){
          case 1:
          	this.getGiftGoodsList(1)
          break;
          case 2:
          	this.getGiftGoodsList(5)
          break;
          case 3:
          	this.getGiftGoodsList(21)
          break;
        }
      },

      //计算器
     valueChange(val) {
        this.value += val;
     	},

      //当type = 3， 点击选中返回选中下标
      getCheckedIndex(index) {
        console.log(index)
      },
      getGiftGoodsList(type){
      	this.$Api.getGiftGoodsList(type,this.PARAMS).then((res)=>{
      		console.log(res)
      		if(res.q.s == 0){
      			if(this.PARAMS.pa == 1){
      				this.goodses = res.q.goodses
      			}else if(this.PARAMS.pa > 1 && res.q.goodses.length > 0){
      				let gl = this.goodses
							this.goodses = gl.concat(res.q.goodses)
      			}
      		}
      	})
      },
      loadMore() {
      	this.loading  = true;
				setTimeout(()=>{
					this.PARAMS = Object.assign({},this.PARAMS,{
						pa:parseInt(this.PARAMS.pa)+1,
					})
					let type = ''
					if(this.index == 1){
						type = 1
					}else if(this.index == 2){
						type = 5
					}else if(this.index == 3){
						type = 21
					}
					this.getGiftGoodsList(type)
		      this.loading  = false;
				},1000)
	    },
    },

    components: {
      Gift,
      Counter
    }
  }
</script>


<style lang="scss">
  #gift {
    height: 100%;
    background: #f0f0f0;

    .gift__tab {
    	position: fixed;
    	top: 1.17rem;
    	left: 0;
    	z-index: 100;
    	width: 100%;
      height: 1.067rem;
      line-height: 1.067rem;
      font-size: 0;
      background: #fff;
      border-bottom: 1px solid #eee;

      .tab {
        display: inline-block;
        width: 33.3%;
        text-align: center;
        font-size: 0.4rem;
        color: #333;
      }

      .tab.active {
        position: relative;
        color: #de7fef;

        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          height: 2px;
          width: 100%;
          background: #de7fef;
        }
      }
    }

    .gift__bottom {
      position: fixed;
      bottom: 0;
      left: 0;
      display: flex;
      justify-content: space-between;
      height: 1.3067rem;
      line-height: 1.3067rem;
      width: 100%;
      font-size: 0.453rem;
      background: #fff;
      border-top: 1px solid #eee;
			z-index: 100;
      .count {
        display: flex;
        .count__text {
          padding: 0 0.6rem 0 0.4rem;
        }
      }

      .submit {
        padding: 0 1.07rem;
        color: #fff;
        background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
      }
    }
  }
</style>




